#app {
  min-height: 100vh;
  height: 100vh;
  max-height: 100vh;
  min-width: 100vw;
  width: 100vw;
  max-width: 100vw; }
  #app > #topbar {
    z-index: 11;
    position: absolute;
    top: 0;
    left: 0; }
  #app > #sidebar {
    position: absolute;
    top: 45px;
    left: 0;
    -moz-box-sizing: border-box;
         box-sizing: border-box;
    height: -webkit-calc(100% - 45px);
    height: -moz-calc(100% - 45px);
    height: calc(100% - 45px);
    z-index: 10;
    box-shadow: 0 0 10px 4px rgba(0, 0, 0, 0.6), inset 0 12px 12px -8px rgba(0, 0, 0, 0.6); }
  #app > #view {
    position: absolute;
    left: 50px;
    top: 45px;
    z-index: 1;
    -moz-box-sizing: border-box;
         box-sizing: border-box;
    width: -webkit-calc(100% - 50px);
    width: -moz-calc(100% - 50px);
    width: calc(100% - 50px);
    height: -webkit-calc(100% - 45px);
    height: -moz-calc(100% - 45px);
    height: calc(100% - 45px);
    max-width: -webkit-calc(100% - 50px);
    max-width: -moz-calc(100% - 50px);
    max-width: calc(100% - 50px);
    max-height: -webkit-calc(100% - 45px);
    max-height: -moz-calc(100% - 45px);
    max-height: calc(100% - 45px);
    overflow: auto;
    background: white;
    box-shadow: inset 0 10px 10px -8px rgba(0, 0, 0, 0.6);
    padding: 8px; }
