body {
  font-family: Tahoma, Verdana, Segoe, sans-serif;
  font-size: 15px;
  font-style: normal;
  font-variant: normal;
  font-weight: 100;
  line-height: 24px; }
  body h1 {
    font-family: Tahoma, Verdana, Segoe, sans-serif;
    font-size: 24px;
    font-style: normal;
    font-variant: normal;
    font-weight: 700;
    line-height: 24px; }
  body h2, body h3 {
    font-family: Tahoma, Verdana, Segoe, sans-serif;
    font-size: 19px;
    font-style: normal;
    font-variant: small-caps;
    font-weight: 700;
    line-height: 24px; }
  body p, body input {
    font-family: Tahoma, Verdana, Segoe, sans-serif;
    font-size: 15px;
    font-style: normal;
    font-variant: normal;
    font-weight: 100;
    line-height: 24px; }
  body blockquote {
    font-family: Tahoma, Verdana, Segoe, sans-serif;
    font-size: 19px;
    font-style: italic;
    font-variant: normal;
    font-weight: 100;
    line-height: 24px; }
  body pre {
    font-family: Tahoma, Verdana, Segoe, sans-serif;
    font-size: 12px;
    font-style: normal;
    font-variant: normal;
    font-weight: 400;
    line-height: 24px; }

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address,
big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl,
dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside,
canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time,
mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline; }
