.mini-schedule {
  width: 200px;
  height: 200px;
  display: grid;
  grid-template-columns: repeat(5, 20%);
  grid-template-rows: repeat(28, 3.57143%);
  border: 1px solid black;
  pointer-events: auto;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }
  .mini-schedule:hover {
    border: 1px solid #d1632b; }
  .mini-schedule.selected {
    border: 1px solid #5faabf; }
  .mini-schedule .mini-schedule-grid-line-x {
    border-bottom: 1px solid rgba(0, 0, 0, 0.2); }
  .mini-schedule .mini-schedule-grid-line-y {
    border-right: 1px solid rgba(0, 0, 0, 0.2); }
  .mini-schedule .mini-schedule-block {
    background: #7fe2ff; }
