.week-calendar {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 70px;
  grid-template-rows: 30px;
  grid-auto-columns: 1fr;
  grid-auto-rows: 1fr;
  border: 1px solid black; }
  .week-calendar * > .center-content {
    -webkit-align-self: center;
        -ms-flex-item-align: center;
            align-self: center;
    margin: 0 auto; }
  .week-calendar > .header {
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    border-left: 1px solid black; }
    .week-calendar > .header * {
      font-weight: bold; }
    .week-calendar > .header.first {
      border-left: 0; }
  .week-calendar > .row-header {
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    border-top: 1px solid rgba(0, 0, 0, 0.25); }
    .week-calendar > .row-header * {
      font-size: 14px; }
    .week-calendar > .row-header.first {
      border-top: 0; }
  .week-calendar > .event-container {
    border: 1px solid black;
    margin: 0 3px 0 4px;
    background: white;
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.6); }
  .week-calendar > .grid-line-x {
    border-top: 1px solid rgba(0, 0, 0, 0.25);
    z-index: -1; }
    .week-calendar > .grid-line-x.first {
      border-top: 0; }
  .week-calendar > .grid-line-y {
    border-left: 1px solid rgba(0, 0, 0, 0.25);
    z-index: -1; }
    .week-calendar > .grid-line-y.first {
      border-left: 0; }
  .week-calendar > .header-row {
    background: rgba(0, 0, 0, 0.25);
    border-bottom: 1px double black;
    margin-right: -1px; }
  .week-calendar > .row-header-row {
    border-right: 1px solid black; }
